<template>
  <div class="container" id="box">
    <div class="show-box" :class="type == 4?'show-box-full':''" v-if="this.is_weixin()">
      <div class="mid">
        <div class="banner" v-if="type != 4"></div>
        <main>
          <img class="head-img" :src="userInfo.faqi_touxiang" alt="">
          <div class="title">{{userInfo.zhuli_nicheng}}</div>
          <div class="desc">
            <p>邀你参与接龙</p>
            <p v-if="type != 4">帮TA赢价值<span style="color:red"> 5000￥ </span>游学之旅专属邀请函！</p>
            <!--<p>点击「复制」 打开 <span>百度校园</span> 公众号</p>-->
            <!--<p>将复制内容在菜单<span>「热门文章」</span>内留言 帮我助力！</p>-->
          </div>
          <div class="top">
            <div class="help">
              <div class="line line-left"></div>
              <h6>已有<span> {{userInfo.supportCount}} </span>人帮TA接龙</h6>
              <div class="line line-right"></div>
            </div>
          </div>
          <div class="scroll-box wrapper" ref="wrapper">
            <vue-better-scroll
              ref="scroll"
              :scrollbar="scrollbarObj"
              :pullDownRefresh="pullDownRefreshObj"
              :autoPullDownRefresh="autoPullDownRefreshObj"
              :startY="parseInt(startY)"
              @pullingDown="onPullingDown">
              <ul class="list-content content" ref="listContent">
                <li class="list-item" v-for="(item,key) in pageData">
                  <span :class="item.youxiao == 0?'valid-chengyu':'invalid-chengyu'">{{item.jielongci}}</span>
                  <div class="help-p">
                    <div>
                      <div style="color:#000" v-if="item.youxiao == 1">
                        <img :src="item.zhuli_touxiang" alt="">
                        <span>{{item.zhuli_nicheng}}</span>
                      </div>
                      <div v-if="item.youxiao == 0" style="color:#999999">
                        等待接龙ing
                      </div>
                    </div>
                    <div class="button">
                      <div v-if="item.youxiao == 0" class="btn"
                           :data-clipboard-text="'我为「' + userInfo.faqi_nicheng +'」' + item.id + '接龙助力：' + item.jielongci"
                           @click="copyLink"
                           style="background:#ffd200;color:#5c5c5c">复制
                      </div>
                      <div v-if="item.youxiao == 1" style="background:#999999;color: #c0c0c0;">已助力</div>
                    </div>
                  </div>
                </li>
              </ul>
            </vue-better-scroll>
          </div>
          <div class="more" v-on:click="autoPullDownRefresh">
            <div class="icon"></div>
          </div>
        </main>
        <div class="liucheng">
          <div>助力方式</div>
          <p>1、【复制】点击上方任意「复制」按钮</p>
          <p>2、【关注】<a :href="baiduCampusUrl">点击链接</a>，关注 百度校园</p>
          <p>3、【留言】<a :href="articleUrl">点击链接</a>，将复制内容在<span style="color:red">文章内</span>留言，接龙成功！</p>
        </div>
        <div class="tip-box" :class="tip == '复制成功'?'success': tip == '复制失败'?'error':''" v-show="tipShow">{{tip}}</div>
        <div class="bottom" v-if="type != 4">
          <div v-on:click="goRank">奖品排行</div>
          <div v-on:click="goToBaiduCampus('show')">我要发起接龙</div>
        </div>
        <div class="jielong-tip" v-show="jlTipShow">
          <div class="jl-tip-box">
            <p>关注百度校园，回复「成语接龙」发起活动</p>
            <div v-on:click="goToBaiduCampus('hide')">好的</div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
      <p style="font-size:20px;text-align: center;margin-bottom:.2rem">关注百度校园公众号</p>
      <img src="../../lib/qrCode.png" alt="">
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  import clipboard from 'clipboard';
  import wxShare from '../../lib/WxShare'
  import BScroll from 'better-scroll'
  import VueBetterScroll from '../../lib/BScroll.vue'

  export default {
    name: "YouXueZhiLv",
    data() {
      return {
        // 这个配置可以开启滚动条，默认为 false。当设置为 true 或者是一个 Object 的时候，都会开启滚动条，默认是会 fade 的
        scrollbarObj: {
          fade: true
        },
        // 这个配置用于做下拉刷新功能，默认为 false。当设置为 true 或者是一个 Object 的时候，可以开启下拉刷新，可以配置顶部下拉的距离（threshold） 来决定刷新时机以及回弹停留的距离（stop）
        pullDownRefreshObj: {
          threshold: 20,
          stop: 40,
          txt: '加载成功'
        },
        autoPullDownRefreshObj: {
          threshold: 90,
          stop: 40,
        },
        // 这个配置用于做上拉加载功能，默认为 false。当设置为 true 或者是一个 Object 的时候，可以开启上拉加载，可以配置离底部距离阈值（threshold）来决定开始加载的时机
        pullUpLoadObj: {
          threshold: -20,
          txt: {
            more: '刷新页面',
            noMore: '已刷新'
          }
        },
        startY: 0, // 纵轴方向初始化位置
        scrollToX: 0,
        scrollToY: 0,
        scrollToTime: 0,
        pageData: [],//初始化页面数据
        formData: [],//参数
        userInfo: {},
        activityId: '',
        type: '',
        faqi_openid: '',
        start: '',
        limit: 10,
        total: '',
        copyContent: '',
        Clipboard: null,
        tipShow: false,
        tip: '',
        loadFlag: true,
        articleUrl: '',
        baiduCampusUrl: 'https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MjM5NjIwOTQyMA==&scene=126&bizpsid=0#wechat_redirect',
        shareConfig: {
          link: '',
          imgUrl: '',
          title: '',
          desc: '最后增加10个名额，点击参与，拿千元校园奖学金'
        },
        jlTipShow: false,
        initStartYflag: true
      }
    },
    mounted() {
      if (this.getQueryParams('activityId') && this.getQueryParams('faqi_openid')) {
        this.activityId = this.getQueryParams('activityId');
        this.faqi_openid = this.getQueryParams('faqi_openid');
        if(this.getQueryParams('type')){
          this.type = this.getQueryParams('type');
          if(this.type == '4'){
            document.title = '深度学习'
          }
        }
        this.initPageData();
        this.getUserInfo();
        this.getUrl();
      }
    },
    methods: {
      autoPullDownRefresh() {
        this.$refs.scroll.autoPullDownRefresh()
      },
      getQueryParams: function (name, url) {
        if (!url) url = location.href
        name = name.replace(/[\[]/, "\\\[").replace(/[\]]/, "\\\]");
        var regexS = "[\\?&]" + name + "=([^&#]*)";
        var regex = new RegExp(regexS);
        var results = regex.exec(url);
        return results == null ? null : results[1];
      },
      getUserInfo() {
        let data = {
          activityId: this.activityId,
          faqi_openid: this.faqi_openid
        }
        axios.post(this.pc_proxy + 'queryJielongUser.do', qs.stringify(data)).then(res => {
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              this.userInfo = res.data.data;
              this.shareConfig.imgUrl = this.userInfo.faqi_touxiang;
              if(this.type == '4'){
                this.shareConfig.title = this.userInfo.zhuli_nicheng + '邀你一起玩成语接龙'
                this.shareConfig.desc = '';
              }else{
                this.shareConfig.title = this.userInfo.zhuli_nicheng + '邀你一起玩成语接龙，帮TA赢价值5000￥百度校园游学之旅邀请函'
              }
              this.shareConfig.link = window.location.href;
              wxShare.ready(this.shareConfig.link, this.shareConfig.imgUrl, this.shareConfig.title, this.shareConfig.desc);
            }
          }
        })
      },
      initPageData() {
        let data = {
          activityId: this.activityId,
          faqi_openid: this.faqi_openid,
          limit: this.limit
        }
        this.formData = data;
        this.getData().then(res => {
        }, (err) => {
          alert(err);
        });
      },
      getData(str) {
        return new Promise((resolve, reject) => {
          axios.post(this.pc_proxy + 'queryJieLongs.do', qs.stringify(this.formData)).then(res => {
            if (res.status === 200) {
              if (res.data.error_no == '0') {
                var length = res.data.data.datas.length;
                res.data.data.datas.reverse();
                for (var i = 0; i < length; i++) {
                  this.pageData.unshift(res.data.data.datas[i]);
                }
                this.Clipboard = new clipboard('.btn');
                if (res.data.data.start != '0') {
                  this.start = res.data.data.start - this.limit;
                  if (this.start < 0) {
                    this.start = 0;
                  }
                  this.total = res.data.data.total;
                } else {
                  this.loadFlag = false;
                }
                resolve('ok');
              } else {
                reject(res.data);
              }
            }
          })
        })
      },
      onPullingDown(e) {
        // 下拉加载
        if (this.loadFlag) {
          this.formData.start = this.start;
          this.getData('more').then(res => {
            this.pullDownRefreshObj.txt = '加载成功！'
            this.$refs.scroll.forceUpdate(true)
          }, (err) => {

          })
        } else {
          setTimeout(() => {
            this.pullDownRefreshObj.txt = '没有更多了！'
            this.$refs.scroll.forceUpdate(true)
          }, 500)
        }
      },
      onPullingUp() {
        // 上拉刷新
        delete this.formData.start;
        this.loadFlag = true;
        this.$refs.scroll.forceUpdate(false)
        this.getData('refresh').then(res => {
          this.$refs.scroll.forceUpdate(true);
          setTimeout(res => {
            this.$refs.scroll.scrollTo(this.scrollToX, this.scrollToY, this.scrollToTime)
          }, 500)
        })
      },
      copyLink() {
        this.Clipboard.on('success', (res) => {
          this.tip = '复制成功'
          this.tipShow = true;
        });
        this.Clipboard.on('error', (res) => {
          this.tip = '复制失败,请手动复制!'
          this.isError = true;
          this.tipShow = true;
        });
        this.disappear();
      },
      disappear() {
        setTimeout(() => {
          this.tipShow = false;
        }, 500)
      },
      goRank() {
        this.$router.push({path: '/rank'});
      },
      getUrl() {
        axios.post(this.pc_proxy + 'queryHotArticle.do').then(res => {
          if (res.status === 200) {
            if (res.data.error_no == '0') {
              this.articleUrl = res.data.data;
            }
          }
        })
      },
      goToBaiduCampus(str) {
        if (str == 'show') {
          this.jlTipShow = true
        }
        if (str == 'hide') {
          this.jlTipShow = false
        }
      }
    },
    watch: {
      pageData(val) {
        if (this.initStartYflag) {
          this.initStartYflag = false;
          this.$nextTick(() => {
            this.$refs.scroll.initScroll()
            this.startY = this.$refs.scroll.scroll.maxScrollY
          })
        }
      },
      startY(val) {
        this.$nextTick(() => {
          this.$refs.scroll.destroy()
          this.$refs.scroll.initScroll()
        })
      }
    },
    components: {VueBetterScroll}
  }
</script>

<style scoped lang="stylus">
  @import "../../assets/icon/font.css"
  .container {
    position relative
    font-family 'fzzy' !important
    .show-box {
      width 100%
      overflow hidden
      background: url("../../assets/youxuezhilv/bg01.png") no-repeat center center
      background-size: cover
      position relative
      .top {
        width 100%
        margin 0 auto
        margin-top .15rem
        .user-info {
          width 4.5rem
          /*height 1.4rem*/
          /*background-color: #3b84a4*/
          /*border-top-right-radius 50px*/
          /*border-bottom-right-radius 50px*/
          color #000
          /*display flex*/
          /*align-items center*/
          > div {
            padding-left .15rem
            p:first-child {
              font-size .3rem
            }
            p:nth-child(2) {
              font-size .2rem
            }
          }
        }
        .help {
          width 100%
          height 0.64rem;
          font-size 0.35rem
          text-align center
          color #3d1209
          margin 0 auto
          overflow: hidden
          position relative
          display flex
          justify-content space-between
          align-items center
          h6 {
            height 100%
            line-height .64rem
            display block
            position absolute
            top 0
            bottom 0
            right 0
            left 0
            margin auto
            z-index: 9
            padding 0 .1rem
            font-size .3rem
            color #5c5c5c
            text-align center
            span {
              color red
              font-size .38rem
            }
          }
          .line {
            width 20%
            height 2px
            border-bottom 2px dashed #406aa1
            margin-top auto
            margin-bottom auto
            z-index: 8
          }
          .line-left {
            margin-left .5rem
          }
          .line-right {
            margin-right .5rem
          }
        }

      }
      .mid {
        padding 0 .25rem
        position relative
        overflow hidden
        margin-top .35rem
        .banner {
          width 7rem
          height 2.38rem
          margin 0 auto
          background: url("../../assets/youxuezhilv/banner.png") no-repeat center center;
          background-size cover
        }
        main {
          width 100%
          height auto
          margin-top .2rem
          background #ffffff
          border-radius 4px
          -webkit-box-sizing: border-box
          -moz-box-sizing: border-box
          box-sizing: border-box
          position relative
          overflow hidden
          font-size 12px
          .head-img {
            border-radius 50%
            width 1.2rem
            height 1.2rem
            display block
            margin-left 0.2rem
            /*margin-left auto*/
            /*margin-right auto*/
            margin-top 0.2rem
            float left
          }
          .title {
            text-align left
            font-size .35rem
            color #4c6995
            margin-left 1.6rem
            margin-top .3rem
          }
          .desc {
            p {
              font-size .25rem
              color #999999
              text-align left
              margin-left 1.6rem;
              margin-top 0.03rem
              span {
                color #d9304b
              }
            }
          }
          .scroll-box {
            width 5.45rem
            height 5.6rem
            overflow hidden
            margin 0 auto
            font-size .14rem
            position relative
            .list-content {
              min-height 5.6rem
              .valid-chengyu {
                color #4c6995
              }
              .invalid-chengyu {
                color #999999
              }
              .list-item {
                display flex
                align-items center
                justify-content space-between
                padding-top .1rem
                height .45rem
                font-size .2rem
                > span {
                  font-size .25rem
                }
                .help-p {
                  width 4.4rem
                  overflow hidden
                  font-size .18rem
                  > div:first-child {
                    > div {
                      float left
                      padding-left 1rem
                      display flex
                      align-items: center
                      font-size .25rem
                      img {
                        height .45rem
                        width .45rem
                        border-radius 50%
                        display block
                      }
                      span {
                        display block
                        margin-left .05rem
                        width 1.5rem
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                      }
                    }
                  }
                  .button {
                    float right
                    padding-right .2rem
                    font-size .25rem
                    > div {
                      border-radius 4px
                      display flex
                      align-items center
                      text-align center
                      padding .05rem .1rem
                    }
                  }
                }
              }
            }
          }
          .more {
            width 100%
            height .5rem
            background #ffffff
            .icon {
              width .2rem
              height .19rem
              background url("../../assets/youxuezhilv/more.png") no-repeat center center
              -webkit-background-size: cover
              background-size: cover
              margin 0 auto
              margin-top .15rem
            }
          }

        }
        .liucheng {
          width 100%
          height 2.2rem
          margin 0 auto
          background #ffffff
          -webkit-border-radius: 4px
          -moz-border-radius: 4px
          border-radius: 4px
          margin-top .2rem
          overflow: hidden
          > div {
            color #406aa1
            text-align center
            font-size .3rem
            margin-top .2rem
          }
          p {
            font-size .25rem
            color #999
