import axios from 'axios';
import wx from 'weixin-js-sdk'
import qs from 'qs'
export class WxShare {
  static ready = (link, imgUrl, title, desc) => {
    var share = (link, imgUrl, title, desc) => {
      let ShareJson = {
        link: link,
        imgUrl: imgUrl,
        title: title,
        desc: desc
      };
      wx.onMenuShareTimeline(ShareJson);
      wx.onMenuShareAppMessage(ShareJson);
    };
    // var refresh_token = (success) => {
    //   axios.get("/campusweixin/js/getJsConfig.do", {"shareUrls":location.href.split("#")[0]}).then();
    // };
    let data = {
      url:location.href.split('#')[0],
      qq:1
    }
    axios.post("/campusweixin/js/getJsConfig.do", qs.stringify(data)).then((data) => {
      wx.config(data.data.data);
      // wx.hideMenuItems({
      //   menuList: ["menuItem:share:timeline", "menuItem:copyUrl", "menuItem:share:appMessage", "menuItem:share:qq", "menuItem:share:weiboApp", "menuItem:favorite", "menuItem:share:facebook", "menuItem:share:QZone", "menuItem:editTag", "menuItem:delete", "menuItem:copyUrl", "menuItem:originPage", "menuItem:readMode", "menuItem:openWithQQBrowser", "menuItem:openWithSafari", "menuItem:share:email", "menuItem:share:brand"] // 要隐藏的菜单项，只能隐藏“传播类”和“保护类”按钮
      // });
      wx.ready(() => {
        share(link, imgUrl, title, desc);
      });
      wx.error((res) => {
        // refresh_token(share(link, imgUrl, title, desc));
      });
    })
  };
}

export default WxShare;



